import { useState } from 'react';

interface FaqItem {
  question: string;
  answer: string;
}

export default function FaqPage() {
  const [openIndex, setOpenIndex] = useState<number | null>(0);

  const faqData: FaqItem[] = [
    {
      question: '取消機制',
      answer: '若您需要取消課程，請至少提前24小時通知我們。提前24小時以上取消，該堂課程可保留並重新安排時間；若未提前24小時通知，該堂課程將視為已使用，無法保留或退款。我們理解計劃可能會有變動，因此建議您盡早與我們聯繫調整課程時間。'
    },
    {
      question: '可以退款嗎',
      answer: '可以的！我們提供彈性的退款機制。若您因故無法繼續課程，剩餘堂數可無條件退還9成費用。例如：若您購買8堂課程方案，已上完3堂，剩餘5堂可退還原價的90%。退款將在申請後7個工作天內處理完成。我們希望讓您在沒有壓力的情況下體驗我們的服務。'
    },
    {
      question: '我家適合上課嗎',
      answer: '大部分的居家空間都適合進行課程！我們建議準備一個約2公尺 x 2公尺的活動空間，地面平坦且周圍沒有尖銳物品即可。不需要特別大的空間，客廳、臥室或書房都可以。教練會在第一堂課時評估您的空間，並根據實際環境調整課程內容與動作設計，確保安全且有效。若您對空間有疑慮，歡迎在預約時與我們討論。'
    },
    {
      question: '需要準備什麼器材',
      answer: '基本上不需要準備任何器材！我們的課程設計以徒手訓練和功能性動作為主，利用您的體重就能達到很好的訓練效果。如果您家中有瑜珈墊，可以準備一張增加舒適度。若課程進展到需要使用輔助器材（如彈力帶、小啞鈴等），教練會提前告知，且這些器材價格親民，容易取得。我們的目標是讓運動變得簡單、不受限制。'
    }
  ];

  const toggleFaq = (index: number) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <div className="min-h-screen bg-[#F6F1E6] py-20">
      <div className="max-w-4xl mx-auto px-8 pt-12">
        {/* Header */}
        <div className="text-center mb-16">
          <h1 className="text-5xl font-bold text-[#0F2E24] mb-4">
            常見問題 FAQ
          </h1>
          <p className="text-lg text-[#8B9A94]">
            關於課程的常見疑問，我們都為您整理好了
          </p>
        </div>

        {/* FAQ List */}
        <div className="space-y-4">
          {faqData.map((faq, index) => (
            <div
              key={index}
              className="bg-white rounded-2xl shadow-md overflow-hidden transition-all duration-300 hover:shadow-lg"
            >
              <button
                onClick={() => toggleFaq(index)}
                className="w-full px-8 py-6 flex items-center justify-between cursor-pointer transition-colors hover:bg-[#F6F1E6]/30"
              >
                <div className="flex items-center gap-4">
                  <div className="w-10 h-10 flex items-center justify-center bg-[#0F2E24] rounded-full text-white font-bold text-sm">
                    {index + 1}
                  </div>
                  <h3 className="text-xl font-semibold text-[#0F2E24] text-left">
                    {faq.question}
                  </h3>
                </div>
                <i
                  className={`ri-arrow-down-s-line text-2xl text-[#0F2E24] transition-transform duration-300 ${
                    openIndex === index ? 'rotate-180' : ''
                  }`}
                ></i>
              </button>

              <div
                className={`overflow-hidden transition-all duration-300 ${
                  openIndex === index ? 'max-h-96' : 'max-h-0'
                }`}
              >
                <div className="px-8 pb-6 pt-2">
                  <div className="pl-14">
                    <p className="text-base text-[#0F2E24] leading-relaxed">
                      {faq.answer}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Contact Section */}
        <div className="mt-16 bg-white rounded-2xl p-10 shadow-md text-center">
          <div className="w-16 h-16 flex items-center justify-center bg-[#0F2E24] rounded-full mx-auto mb-6">
            <i className="ri-question-line text-3xl text-white"></i>
          </div>
          <h3 className="text-2xl font-bold text-[#0F2E24] mb-3">
            還有其他問題嗎？
          </h3>
          <p className="text-base text-[#8B9A94] mb-6">
            如果您有任何其他疑問，歡迎隨時與我們聯繫，我們很樂意為您解答
          </p>
          <button
            onClick={() => window.REACT_APP_NAVIGATE('/book')}
            className="bg-[#0F2E24] text-[#F6F1E6] px-8 py-3 rounded-full text-base font-medium hover:scale-105 transition-all duration-300 shadow-md whitespace-nowrap cursor-pointer"
          >
            立即預約諮詢
          </button>
        </div>
      </div>
    </div>
  );
}
